import React,{useState} from "react";
import CustomModal from "../../components/Modal/customModal";

const DocumentCreationModal = ({ show, onHide, onSave,}) => {
    const [type, setType] = useState("Document");
    const [fileInfo, setFileInfo] = useState(null);
    const [comments, setComments] = useState("");

    const [typeDropDownData, setTypeDropDownData] = useState([
        {
            label: "Document",
            value: "Document",
        }

    ])

    const handleCreateDocument = () => {
        onSave(type, fileInfo, comments);
        onHide();
    };

    return (
        <CustomModal
            show={show}
            onHide={onHide}
            title="New Document"
            footerButtons={[
                {
                    label: "Create",
                    variant: "primary",
                    onClick: () => {
                        handleCreateDocument();
                    },
                },
                {
                    label: "Close",
                    variant: "secondary",
                    onClick: onHide,
                },
               
            ]}

        >
    <div className="row col-12">
            <div className="col-3">
                <label>Type</label>
                </div>
                <div className="col-9">
                <select
                    className="form-select mb-2"
                    value={type}
                    onChange={(e) => setType(e.target.value)}
                >
                    <option value="">-- Select Column --</option>
                    {typeDropDownData.map((item) => (
                        <option key={item?.val} value={item.val}>
                            {item.label}
                        </option>
                    ))}
                </select>
            </div>
        </div>
        <div className="row col-12">
            <div className="col-3">
                <label>Comments</label>
                </div>
                <div className="col-9">
                    <input type="text" className="form-control mb-2" value={comments} onChange={(e)=>{setComments(e.target.value);}}></input>
                </div>
                </div>
        <div className="row col-12">
            <div className="col-3">
                <label>Select File</label>
                </div>
                <div className="col-9">
                    <input type="file"  id="formFile" className="form-control" onChange={(e)=>{setFileInfo(e.target.files[0]);}}></input>
                </div>
                </div>

        </CustomModal>
    );
}
export default DocumentCreationModal;
      